.modal {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
    z-index: 2;
    
    .content {
        padding: 20px;
        border-radius: 12px;
        background-color: white;
        width: 35vw;
        display: flex;
        flex-direction: column;
        transform: scale(0.5);
        transition: 0.4s all;
    }

    .contentActive {
        transform: scale(1);
    }
}

.modalActive {
    pointer-events: all;
    opacity: 1;
}
